import uniIcon from "@/components/uni-icon/uni-icon.vue";
export default {
	components:{
		uniIcon,
	},
	data() {
		return {
			height:0,
			userData:{},
			name:"",
			typeName:"",
			startTime:"",
			tel:"",
			address:"",
			cost:0,
			time:0,
			freetime:0,
			setCost:0,
			setTime:"",
			// 遗忘
			isApply: false,
			remark:"",
			textNum:0,
		}
	},
	onLoad() {
		this.height = uni.getSystemInfoSync().windowHeight;
		uni.showLoading({mask:true});
		this.getData();
	},
	watch:{
		remark(val){
			this.textNum = this.remark.length
		},
		immediate: true,
		deep: true
	},
	methods: {
		getData(){
			uni.request({
				url: this.apiServer + 'cabinetRentEmploymentRecord/getTheUserHireDetails',
				method: 'POST',
				data:{
					fkCabinetRecordStatusNo:"0001"
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						if(!!res.data.data){
							let data = res.data.data;
							this.startTime = data.startTime.split('.')[0];
							this.typeName = data.subCabinetType.typeName;
							this.typeName = data.subCabinetType.typeName;
							this.time = data.subCabinetType.settlingTime;
							this.cost = parseFloat(data.subCabinetType.rent).toFixed(2);
							this.userData = data;
							this.getCaseData(data.fkCabinetNo);
							this.getNormData(data.fkSubCabinetTypeNo);
							this.getTelData(data.fkSpaceCharacterNo);
						}else{
							uni.showModal({
								title: '提示',
								content: '您无包裹在存或已申请物品遗忘',
								confirmText: '返回首页',
								showCancel: false,
								success: function (res) {
									if (res.confirm) {
										uni.navigateBack({url: '../index/index'});
									}
								}
							});
						};
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				},
				complete: (res) => {
					uni.hideLoading();
				}
			});
		},
		getCaseData(No){
			uni.request({
				url: this.apiServer + 'cabinetManage/getCabinetOne',
				method: 'POST',
				data:{
					cabinetNo:No
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if(res.data.status == 200){
						let data = res.data.data;
						this.name = data.nameLocker;
						this.address = data.cabinetAddress;
						this.freetime = data.freeTime;
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			});
		},
		getNormData(No){
			uni.request({
				url: this.apiServer + 'subCabinetType/getSubCabinetTypeOne',
				method: 'POST',
				data:{
					subCabinetTypeNo:No
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if(res.data.status == 200){
						let data = res.data.data;
						this.setCost = parseFloat(data.rent).toFixed(2);
						this.setTime = data.settlingTime;
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			});
		},
		getTelData(No){
			uni.request({
				url: this.apiServer + 'spaceCharacter/getSpaceCharacterOne',
				method: 'POST',
				data:{
					spaceCharacterNo:No
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						this.tel = res.data.data.spaceCharacterMobile;
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			});
		},
		forgotten(){
			let that = this;
			uni.showModal({
				title: '提示',
				content: '请提交申请前，电话联系柜子管理员!',
				cancelText: '返回',
				confirmText: '填写备注',
				success: function (res) {
					if (res.confirm) {
						that.isApply = true;
					}
				}
			});
		},
		telCall(){
			uni.makePhoneCall({
				phoneNumber: this.tel
			});
		},
		priceOrder(data){
			uni.request({
				url: this.apiServer + 'cabinetRentEmploymentRecord/orderAndSettlement',
				method: 'POST',
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						this.payOrder(res.data.data,data);
					}else if(res.data.status == 101){
						if(data){
							this.openCase();
						}else{
							this.postForget(this.userData);
						};
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			});
		},
		postForget(data){
			if(this.remark.length > 0){
				uni.request({
					url: this.apiServer + 'cabinetRentEmploymentRecord/applyForForgetting',
					method: 'POST',
					data:{
						fkCabinetRecordNo: data.cabinetRecorNo,
						forgetRemark: this.remark
					},
					header:{
						'content-Type': 'application/x-www-form-urlencoded',
						'token': uni.getStorageSync('opid')
					},
					success: (res) => {
						if(res.data.status == 200){
							uni.showToast({
								title: '申请成功，请去首页——物品遗忘中查看',
								icon: 'none',
								duration: 2000
							});
							setTimeout(() => {
								uni.navigateBack({url: '../index/index'});
							}, 2000);
						}else if(res.data.status == 588){
							this.checkLogin();
							uni.showToast({
								title: '登录失效，请再试一次',
								icon: 'none',
								duration: 2000
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								icon: 'none',
								duration: 2000
							});
						}
					},
					fail: (res) => {
						uni.showToast({
							title: '网络异常',
							icon: 'none',
							duration: 2000
						});
						setTimeout(() => {
							uni.navigateBack({url: '../index/index'});
						}, 2000);
					}
				});
			}else{
				uni.showToast({
					title: '请填写备注信息',
					icon: 'none',
					duration: 2000
				});
			}
		},
		payOrder(data,type){
			if(type){
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: data.timeStamp,
					nonceStr: data.nonceStr,
					package: data.package,
					signType: data.signType,
					paySign: data.paySign,
					success: (res) => {
						if(res.errMsg == 'requestPayment:ok'){
							this.openCase();
						};
					},
					fail: (err) => {
						if(err.errMsg == 'requestPayment:fail cancel'){
							uni.showToast({
								title: '已取消支付',
								icon: 'none',
								duration: 2000
							});
						}else{
							uni.showToast({
								title: '支付失败',
								icon: 'none',
								duration: 2000
							});
						}
					}
				});
			}else{
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: data.timeStamp,
					nonceStr: data.nonceStr,
					package: data.package,
					signType: data.signType,
					paySign: data.paySign,
					success: (res) => {
						if(res.errMsg == 'requestPayment:ok'){
							this.postForget(this.userData);
						};
					},
					fail: (err) => {
						if(err.errMsg == 'requestPayment:fail cancel'){
							uni.showToast({
								title: '已取消支付',
								icon: 'none',
								duration: 2000
							});
						}else{
							uni.showToast({
								title: '支付失败',
								icon: 'none',
								duration: 2000
							});
						}
					}
				});
			};
		},
		openCase(){
			uni.request({
				url: this.apiServer + 'cabinetProcessing/customerStatementOpenTheCabinet',
				method: 'POST',
				data:{
					cabinetRecordNo:this.userData.cabinetRecorNo
				},
				header:{
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						uni.showToast({
							title: '开柜成功',
							icon: 'none',
							duration: 2000
						});
						setTimeout(() => {
							uni.navigateBack({url: '../index/index'});
						}, 2000);
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg+'请联系客服',
							icon: 'none',
							duration: 2000
						});
					};
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				}
			})
		}
	}
}
